.offers{
    width: 75%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0 140px;
    margin-bottom: 159px;
    background: linear-gradient(180deg , #fde1ff 0% ,#e1ffea22 60%);
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.offers-left > h1{
    color: #171717;
    font-size: 80px;
    font-weight: 600;
}
.offers-left > p{
    color: #171717;
    font-size: 22px;
    font-weight: 600;
}
.offers-left > button{
    width: 280px;
    height: 70px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}
@media (max-width:992px) {
    .offers{
        flex-direction: column-reverse;
        height: 100%;
        width: 100%;
        margin-top: 100px;
    }
    .offers-right{
        padding: 20px 0;
    }
}
@media (max-width:992px){
    .offers-right{
        justify-content: center;
    }
    .offers-left > h1{
        font-size: 36px;
    }
}