.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 100px;
}
.new-collections  h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
    text-align: center;
}
.new-collections > hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.collections{
    margin-top: 50px;
    display: flex;
    gap: 30px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding:0 12px;
}

@media (max-width:992px) {
    .collections{
        gap: 32px;
    }
}

@media(max-width:600px){
    .new-collections  h1{
        font-size: 40px;
    }
}