.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
}
.footer-logo{
    display: flex;
    align-items: center;
    gap: 20px;
}
.footer-logo > p {
    color: #383838;
    font-size: 46px;
    font-weight: 700;
}
.footer-links{
    display: flex;
    align-items: center;
    list-style-type: none;
    gap: 50px;
    color: #252525;
    font-size: 20px;
}
.footer-links > li {
    cursor: pointer;
}
.footer-social-icons{
    display: flex;
    gap: 20px;

}
.icons-container{
    padding: 10px;
    padding-bottom: 6px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
}
.footer-copyrights{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 20px;
}
.footer-copyrights > hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}

@media (max-width:600px){
    .footer-links{
        gap: 25px;
        font-size: 16px;
        flex-wrap: wrap;
        justify-content: center;
    }
    .footer-copyrights p{
        padding: 0 8px;
        text-align: center;
    }
}